.app {
  min-height: 100vh;
  background: #CCC;

  .header {
    background: royalblue;
    height: 60px;
    padding: 0 32px;
    display: flex;
    align-items: center;

    .logo {
      font-size: 24px;
      color: #FFF;
      font-weight: bold;
    }
  }

  .content {
    min-height: calc(100vh - 60px);
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    .form {
      padding: 16px 8px 16px 16px;

      .inner {
        .formik {
          position: relative;
          padding: 16px;
          box-sizing: border-box;

          .textarea {
            //height: 500px;
            background: #FFF;
          }

          .submitBtn {
            position: absolute;
            top: -46px;
            left: calc(50vw - 16px);
            transform: translate(-50%, -50%);
            width: 200px;
          }
        }
      }
    }

    .result {
      padding: 16px 16px 16px 8px;
      box-sizing: border-box;

      .inner {
        padding: 16px;
      }

    }

    .form, .result {
      box-sizing: border-box;

      .inner {
        min-height: calc(100vh - 92px);
        border: 2px solid royalblue;
        border-radius: 16px;
        box-sizing: border-box;
      }
    }

  }
}
